.styles_navbar__3jXu7 {
	display: flex;
	align-items: center;
	justify-content: flex-end; /* Zmiana wartości na flex-end */
	background-color: #065c4f;
	padding: 10px;
	color: white;
  }
  
  .styles_navbar__3jXu7 h1 {
	font-size: 35px;
	margin: 0;
	margin-right: auto;
  }
  
  .styles_navbar__3jXu7 button {
	border: none;
	outline: none;
	padding: 10px 20px;
	background-color: transparent;
	color: white;
	font-size: 20px;
	cursor: pointer;
  }
  
  .styles_navbar__3jXu7 button:first-child {
	margin-left: auto; /* Dodaj tę linię, aby przesunąć pierwszy przycisk na prawo */
  }
  
  .styles_navbar__3jXu7 button:hover {
	background-color: rgba(255, 255, 255, 0.2);
	transition: background-color 0.3s ease;
  }

 /* Przykładowe style dla responsywności */
.styles_heading-1__2DBPj {
	/* Twoje istniejące style dla tytułu */
	font-size: 36px;
  }
  
  .styles_heading-2__1QpiC {
	/* Twoje istniejące style dla tytułu */
	font-size: 24px;
  }
  
  /* Responsywność dla ekranów o szerokości mniejszej niż 768px */
  @media (max-width: 768px) {
	.styles_heading-1__2DBPj {
	  font-size: 28px; /* Nowy rozmiar czcionki dla ekranów mniejszych niż 768px */
	}
  
	.styles_heading-2__1QpiC {
	  font-size: 18px; /* Nowy rozmiar czcionki dla ekranów mniejszych niż 768px */
	}
  }
  
